
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  font-family: Georgia, serif;
}

.App {
  font-family: sans-serif;
  text-align: center;
  width:100%;
  display: flex;
  flex-direction: column;
}

.App .product-grid {
  display:grid;
  grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  align-items: center;
  justify-items: center;
}

