.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4rem;
}

.nav h2 {
    margin: 0 1rem;
    cursor: pointer;
}
.nav .menu {
    list-style: none;
    display: flex;
}

.nav ul li {
    margin:1rem;
    cursor: pointer;
}

.nav .menu-icon {
    margin: 0 1rem;
    cursor: pointer;
    display: none;
}

@media screen and (max-width: 760px) {

    .nav .menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 90vh;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.6s ease;
      }
    
    .nav .active {
        position: absolute;
        left:0;
        top:4rem;
        background: white;
        opacity:1;
        display: flex;
        flex-direction: column;
        height:90vh;
        width:100%;
        transition: all 0.6s ease;
        align-items: center;
        
        
    }

    .nav .menu-icon {
        display: block;
    }
}